<template>
  <v-container fluid class="mt-6">
    <div class="lineCharts-page">
      <v-row>
  <v-card class="mx-auto" max-width="800">
    <!-- 상단에 카드 이미지 표시, 파일경로는 assets 기준 -->
    <v-row align="center">
    
      <!-- 우측에 안내글 표시 -->
      <v-col cols="8">
        <v-card-title primary-title>
          <div class="body-1"> 
           qr scan screen
          </div>
        </v-card-title>
      </v-col>
    </v-row>
    <v-row align="center">
      <v-col offset="1" cols="5">
        <!-- 하단에 푸시알림 가입 버튼 표시 -->
        <v-btn block large @click="reload()" color="orange" dark>
         qr scan
        </v-btn>
      </v-col>
      <v-col cols="5">
        <!-- 하단에 푸시알림 해제 버튼 표시 -->
        <v-btn block large @click="turnoff()" color="orange" dark>
          scan off
        </v-btn>
      </v-col>
    </v-row>
    <v-snackbar v-model="bMsg">
      scan off 됨.
      <v-btn color="orange" text @click="bMsg = false">닫기</v-btn>
    </v-snackbar>

 <div>
    <p class="error">{{ error }}</p>

    <p >Last result: <b>{{ result }}</b></p>

    <qrcode-stream :key="_uid" :camera="camera"  @decode="onDecode" @init="onInit" :track="paintOutline"  v-if="!destroyed">
      <div v-if="validationSuccess" class="validation-success">
        This is a Bladder Code
      </div>

      <div v-if="validationFailure" class="validation-failure">
        This is NOT a Bladder CodeURL!
      </div>

      <div v-if="validationPending " class="validation-pending">
        Long validation in progress...
      </div>

    </qrcode-stream>
  </div>     

  </v-card>
      </v-row>
  </div>
  </v-container>
</template>


<script>
  // 파이어베이스에서 oSubscriptionsinDB 객체 변수 가져옴
  import Vue from "vue";
  import VueQrcodeReader from "vue-qrcode-reader";
 
  Vue.use(VueQrcodeReader);

  export default {
    data() {
      return {
        bMsg: false, // 버튼이 눌렸을 때 안내 메시지 표시여부
        token : "",
        result : "",
        error : '',
        isValid: undefined,
        pending: false,
        camera: 'off',
        vcheck : false,
        destroyed: false
      }
    },

    computed: {
      validationPending () {
        return this.pending && this.camera === 'off'
      },

      validationSuccess () {
        return this.isValid === true
      },

      validationFailure () {
        return this.isValid === false
      }
    },
    methods: {


      async turnoff () {
        this.destroyed = true

        await this.$nextTick()

        this.destroyed = false
        this.camera = 'off';
        this.result = '';

      },

      async reload () {
        this.destroyed = true

        //await this.$nextTick()

        this.destroyed = false
        this.camera = 'auto';
         this.result = '';
        //window.setTimeout( this.turnCameraOff(), 3000);
        await this.timeout(10000) 
        this.turnoff();
      },

      async onDecode (result) {
        this.result = result;
        //this.turnCameraOff();
         this.pending = true; 
        //유효성 체크
        await this.timeout(1000)
        this.isValid = result.startsWith('Hello')

        await this.timeout(1000);
        this.destroyed = true
        await this.$nextTick()
        this.destroyed = false
        this.camera = 'off';
        this.pending = false;

      },

      timeout(ms) {
        return new Promise(resolve  => { window.setTimeout(resolve, ms)})
      },

 
      turnCameraOn () {
        this.camera = 'auto'
      },

      turnCameraOff () {
        this.camera = 'off'
      },

      async onInit (promise) {
      try {
        await promise

        this.isValid = undefined;

      } catch (error) {
        
        if (error.name === 'NotAllowedError') {
          this.error = "ERROR: you need to grant camera access permisson"
        } else if (error.name === 'NotFoundError') {
          this.error = "ERROR: no camera on this device"
        } else if (error.name === 'NotSupportedError') {
          this.error = "ERROR: secure context required (HTTPS, localhost)"
        } else if (error.name === 'NotReadableError') {
          this.error = "ERROR: is the camera already in use?"
        } else if (error.name === 'OverconstrainedError') {
          this.error = "ERROR: installed cameras are not suitable"
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = "ERROR: Stream API is not supported in this browser"
        }
      }
    },

    paintOutline (detectedCodes, ctx) {
      for (const detectedCode of detectedCodes) {
        const [ firstPoint, ...otherPoints ] = detectedCode.cornerPoints

        ctx.strokeStyle = "red";
        ctx.lineWidth = 5;

        ctx.beginPath();
        ctx.moveTo(firstPoint.x, firstPoint.y);
        for (const { x, y } of otherPoints) {
          ctx.lineTo(x, y);
        }
        ctx.lineTo(firstPoint.x, firstPoint.y);
        ctx.closePath();
        ctx.stroke();
      }
    },


      qrscan() {
        // this의 값 임시 저장

      },
      // 푸시알림을 위해 서비스워커 점검 및 푸시서버 설정
      scanoff() {
        // this의 값 임시 저장
        
      },
      // 새로운 구독자 등록 처리
  }
  }

</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}

.validation-success,
.validation-failure,
.validation-pending {
  position: absolute;
  width: 100%;
  height: 100%;

  background-color: rgba(255, 255, 255, .8);
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 10px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
.validation-success {
  color: green;
}
.validation-failure {
  color: red;
}
</style>